import { prisma } from "../prisma"

export const getArticleByQuery = async (searchText:string) => {
    const articles = await prisma.article.findMany({
        where:{
            OR:[
                {
                    title:{
                        contains:searchText, mode:'insensitive'}
                },
                {
                    category:{
                        contains:searchText, mode:'insensitive'
                    }
                }
                
            ]
        },
        include:{
            author:{
                select:{
                    name:true,
                    email:true,
                    imageUrl:true
                }
            }
        }
    });

    return articles;
}